import Button from '@mui/material/Button';
import { ethers } from 'ethers';
import React, {useEffect, useState} from 'react';
import style from "./App.module.scss";

export const App = () => {
    const [errorMessage, setErrorMessage] = useState<string | null>(null);
    const [currentAccountAddress, setCurrentAccountAddress] = useState<string | null>(null);
    const [balance, setBalance] = useState<string | null>(null);
    const [connectButtonLabel, setConnectButtonLabel] = useState("Connect metamask");

    const onConnectHandler = async () => {
        if (window.ethereum) {
            try {
                const accounts = await window.ethereum.request({method: 'eth_requestAccounts'});
                accountChangeHandler(accounts[0]).then()
            } catch (e: any) {
                console.log(e)
                if (e.message) {
                    setErrorMessage(e.message)
                } else {
                    setErrorMessage("error")
                }
            }


        } else {
            setErrorMessage("Please install MetaMask")
        }
    }

    const accountChangeHandler = async (newAccount: string) => {
        setCurrentAccountAddress(newAccount);
        await getBalance(newAccount);
        setErrorMessage("");
    }

    const getBalance = async (newAccount: string) => {
        try {
            const balance = await window.ethereum.request({
                method: 'eth_getBalance',
                params: [newAccount, 'latest']
            });
            setBalance(ethers.utils.formatEther(balance))
        } catch (e: any) {
            if (e.message) {
                setErrorMessage(e.message)
            } else {
                setErrorMessage("error")
            }
        }
    }

    const chainChangedHandler = () => {
        window.location.reload();
    }

    useEffect(() => {
        if (window.ethereum) {
            window.ethereum.on('accountsChanged', (accounts: any) => accountChangeHandler(accounts[0]));
            window.ethereum.on("chainChanged", chainChangedHandler);

            return () => {
                window.ethereum.removeListener('accountsChanged', (accounts: any) => accountChangeHandler(accounts[0]));
                window.ethereum.removeListener("chainChanged", chainChangedHandler);
            }
        }
    }, [])

    return (
        <div className={style.app}>
            <h1>Connect to MetaMask</h1>
            <div className={style.block}>
                <h2>Connect using window.ethereum</h2>
                <Button variant="contained"
                        className={style.btn}
                        onClick={onConnectHandler}
                >
                    {connectButtonLabel}
                </Button>
                {
                    currentAccountAddress && <p className={style.info}>
                        <span>Address:</span> <span>{currentAccountAddress}</span>
                    </p>
                }
                {
                    balance &&
                    <p className={style.info}>
                        <span>Balance:</span> <span>{`${balance} ETH`}</span>
                    </p>
                }
            </div>

            {
                errorMessage && <p className={style.error}>
                    {errorMessage}
                </p>
            }


        </div>
    );
}

